.Form{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    font-size: 1.875rem;
    height: 60vh;
    color: white;
    font-family:Montserrat, Helvetica, Verdana, Roboto, sans-serif !important;
    max-width: 100%;
}
.first_block{
    width: 55%;
    text-align: center;
    margin-top: 10%;
    display: flex;
    justify-content: center;
}
.second_block{
    display: flex;
    align-items: center;
    justify-content: center;
    width:100%;
    flex-direction: column;
}
.second_block input{
    outline:none;
    display: block;
    padding: 0 15px;
    margin: 10px 0 15px;
    border-width: 0;
    line-height: 40px;
    border-radius: 20px;
    color: white;
    background: rgba(255,255,255,.2);
    font-size: calc(1rem + 1.23vw);
    font-family: 'Open Sans Condensed', sans-serif;
    /* font-family: 'Libre Caslon Display', serif;
    font-family: 'Monoton', cursive;
    font-family: 'Economica', sans-serif; */
    text-align: center;
    width: 50vw;
    height: 50px;
}
.second_block span{
    height: 40px;
}
.example{
    display: flex;
    color: white;
    font-Size: 1rem;
    line-Height: 1.375;
    font-Weight: 500;
}
